import React,{Component} from 'react';
import Person from './Person';


class App extends Component{
   state={
       Person:[{name:"vikash", from:'ranikhet', contect:'9319647899', id:"dflkjadsfj"},
       {name:"ajay", from:'binagudi', contect:'9086647899', id:"lkjadsfj"},
       {name:"pankaj", from:'champawat', contect:'9700797979', id:"jads"},
       {name:"sandeep", from:'kashipur', contect:'9087689087', id:"lk"}],
       show:false,
   } 
     showHandler=()=>{
         let sh= !this.state.show;
         this.setState({show:sh});    
     }
     
     removeHandler=(index)=>{
       let per=[...this.state.Person];
    per.splice(index, 1);
    this.setState({Person:per});

   }


     update=()=>{
         this.setState({
            Person:[{name:"vikka", from:'ranikhet(almora)', contect:'9978647899', id:"dfadsfj"},
            {name:"ajay", from:'gadwal', contect:'9086647899', id:"lkjadsfj"},
            {name:"pakiya", from:'chamoli', contect:'9700797979', id:"jads"},
            {name:"sandy", from:'delhi', contect:'9087689087', id:"lk"}]
             
         })
     }
    render(){

     const upper={
        textAlign:"center",
        width:"100%",
        backgroundColor:"gray",
        color:'white'
      }
   const per={
     backgroundColor:"skyBlue"

   }
      
   
        let person=null;
        let button =null;
      if (this.state.show){
       person=( 
         
            this.state.Person.map((cl, index)=>{
          return  <Person name={cl.name}
                 from={cl.from}
                 contect={cl.contect}
                 key={cl.id}
                 inde={index +1}
                 click={()=>this.removeHandler(index)}
             /> 
            
            })
          
         );

         button= (<button onClick={this.update}> update</button>);
         per.backgroundColor="red";

      }
 
         return(
            <div>
            <div style={upper}>    
            <h1> Welcome to Employees detail </h1>
            <p>click on the below botton to show the detail of the Employee</p>
            <p> Click in the information for delete the information.</p>
            </div>
            <button style={per} onClick={this.showHandler}> All employee</button>
          
            {person}
            {button}
             
          </div>
         )
        
    

        }}

export default App;

